@media all and (max-width: 620px) {
	.list__item--wrapper {
		.input-wrapper {
			flex: 1 1;
			display: flex;
			min-width: 100px;
			max-width: 450px; } } }

@media all and (max-width: 420px) {
	.list__item--wrapper {
		.input-wrapper {
			min-width: 100px;
			max-width: 250px; } } }
